<template>
    <v-container>
        <v-layout>
            <v-card height="70"></v-card>
        </v-layout>
        <v-layout>
            <v-flex 10>
                <my_card
                         :box="true"
                         :chart_data="data"
                         :chart_setting="setting"
                         :title="title"
                         :yAxis="y_name"
                         :xAxis="x_name"
                ></my_card>
            </v-flex>
        </v-layout>
        <v-btn
                absolute
                dark
                fab
                bottom
                right
                color="blue"
                @click="change()"
        >
            <v-icon>arrow_forward_ios</v-icon>
        </v-btn>
    </v-container>
</template>

<script>
    // @ is an alias to /src
    import my_card from "../components/my_card";
    export default {
        name: 'home_hist',
        components: {
            my_card
        },
        mounted() {
            this.axios.get('http://3.84.236.24:3305/data_table/box_' + this.val).then(response =>
                (
                    this.data.rows = response.data.data,
                        this.title = response.data.title,
                        this.x_name = response.data.x_name,
                        this.y_name = response.data.y_name                )
            )
        },
        data() {
            return {
                val:1,
                setting: {
                    legendName: {
                        '日K': 'kanji'
                    },
                    showDataZoom: true,
                    start: 0,
                    end: 100,
                    upColor: "#90CAF9"
                },
                title: "",
                x_name: "",
                y_name: "",
                data: {
                    columns: ['strokes', 'open', 'close', "highest", "lowest"],
                    rows: [],
                }


            }
        },
        methods: {
            change() {
                this.val += 1;
                if (this.val > 2) this.val = 1;
                this.axios.get('http://3.84.236.24:3305/data_table/box_' + this.val).then(response =>
                    (
                            this.data.rows = response.data.data,
                        this.title = response.data.title,
                            this.x_name = response.data.x_name,
                         this.y_name = response.data.y_name
            )
                )
            }
        }
    }
</script>
